import React from "react";
import {MDBContainer} from "mdb-react-ui-kit";

function Offer2(){
    return <MDBContainer breakpoint="lg"><h1 className="about_h1">órák</h1>
        <div className="container">
            <div className="row">
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/logan-weaver-u76Gd0hP5w4-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Csoportos órák</h2>
                                <p>Minden héten több alkalommal csoportos órákat is tartok, ahol jóhangulatban,  másokkal közösen fejlődhetsz. Ha az egyéni alkalomra bérletet váltasz hozzám, egy csoportos órán ingyen részt is vehetsz.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/alexandra-tran-fS3tGOkp0xY-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Házhoz megyek</h2>
                                <p>Nem akarsz az edzőteremben mások előtt edzeni? Mivel új módszerekkel dolgozom, ezért nem szükséges a konditerem! Akár az otthonodban is segítek elérni azt az alakot, amelyet megálmodtál magadnak! Edzőgépek nélkül.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div></MDBContainer>
}

export default Offer2;
