import React from "react";
import {MDBContainer} from "mdb-react-ui-kit";

function About(){
    return <MDBContainer breakpoint="lg">
        <div className={"aboutSquare"}><div className={"aboutSquareInner"}><p className="about_p">Szia! <br/><br/>
            <a href="">Betti vagyok</a> és alakaformálással foglalkozom,
            azaz <a href="">személyi edző</a> vagyok! <br/><br/>
            Nem átlagos módszerekkel dolgozom,
            a legváltozatosabb feladatokat építem bele az edzéstervekbe, amelyet <a href="">személyre szabottan</a> készítek el minden vendégemnek. <br/><br/>
            Együtt dolgozva nem csak egy jó alakot, hanem
            egy <a href="">egészséges életmódot</a> is kialakítunk! <br/><br/>
            Megmutatom neked, hogy a <a href=""> mozgás szenvedély</a>,
            és ez a lelkesedés nem csak egy nyári
            beach body erejéig tart.</p>
            <button className="btn fitButton">Tudj meg rólam többet!</button>
        </div>
        </div>
        <div className="float-end about_left">
            <h1 className="about_h1">Rólam</h1>
            <img className="aboutImg " src={require('./img/betti_v4.jpg')} alt="belak_betti_trainer"/>
        </div>
    </MDBContainer>
}

export default About;
