import React from "react";
import {MDBContainer} from "mdb-react-ui-kit";

function Offer(){
    return <MDBContainer breakpoint="lg"><h1 className="about_h1">Edzéseim</h1>
        <div className="container">
            <div className="row">
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/victor-freitas-WvDYdXDzkhs-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Egyéni alkalmak</h2>
                                <p>Egy állapotfelmérést követően személyre szabott edzésterv szerint haladunk előre az alakod formálásában. Megmutatom neked azt, hogy milyen eredményes és hasznos lehet egy óra aktív mozgás.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/meghan-holmes-buWcS7G1_28-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Páros edzés</h2>
                                <p>Nem szeretsz egyedül edzeni? Gyertek párban, így közösen motiválhatjátok egymást a feladatok során! Az eredmény megosztott figyelem mellett is garantált - senkinél sem marad szárazon a törölköző.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <div className="offerSquare">
                        <div className="offerSquareInner">
                            <img className="offerImg "  src={require('./img/kelly-sikkema-IZOAOjvwhaM-unsplash.jpg')} alt=""/>
                            <div className="innerSquare">
                                <h2>Bérlet</h2>
                                <p>Hosszútávon járnál hozzám?
                                    10 alkalmas bérletet válthatsz nálam, amellyel időt és pénzt spórohatsz meg. A megfelelő edzésterv a rendszeresség
                                    alapjain nyugszik - ez pedig
                                    egy új életmód kialakításának kulcsa. </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div></MDBContainer>
}

export default Offer;
